<template>

</template>

<script>
import Bus from "../../../utils/Bus";

export default {
  name: "OrgCenterForComapnyUser",
  destroyed(){
    Bus.$off('globalMsgForCompanyUser');
  },
  mounted() {
    var that = this;
    Bus.$on('globalMsgForCompanyUser',function(val){//监听各组件传递的 globalMsg 事件
      console.log("监听到的消息："+JSON.stringify(val));
      this.tmpCaption = [];
      for(var i1=0;i1< val.caption.length;i1++){
        this.tmpCaption[i1]= this.customGlobal.decrypt(val.caption[i1],this.customGlobal.cryptoKey);
      }
      console.info("机构名称："+this.tmpCaption);

    });
  },
}
</script>

<style scoped>

</style>



